import PullTo from 'vue-pull-to'
  export default {
    name:'orderList',
    data(){
      return{
        mydataList:''
      }
    },
    components: {
      PullTo
    },
    props:['dataList'],
    methods:{
      refresh(loaded) {
        setTimeout(() => {
          //this.nowList.unshift(this.nowList[0]);
          loaded('done');
        }, 2000);
      },
      geMore(loaded){
        setTimeout(()=>{
          //console.log(this.nowList)
          //this.nowList = this.listData;
          loaded('done')
        },2000)
      },
    },
    watch:{
      dataList:function(){
          this.mydataList = this.dataList;
          //console.log(this.mydataList)
      }
    }

  }



